@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?s8ttmi');
  src:  url('fonts/icomoon.eot?s8ttmi#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?s8ttmi') format('truetype'),
    url('fonts/icomoon.woff?s8ttmi') format('woff'),
    url('fonts/icomoon.svg?s8ttmi#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-show_pass:before {
  content: "\e900";
}
.icon-correct-symbol:before {
  content: "\e901";
}
.icon-down-chevron:before {
  content: "\e902";
  color: #014a9e;
}
